.navbar {
    background-color: brown;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    padding: 5px 10%;
}
.logo {
    background-color: transparent;
    display: flex;
    align-items: center;
}
.logo img{
    height: 70px;
    width: auto;
}

.logo img:hover{
    transform: scale(1.2);
    transition: all 0.3s;
}

.links a {
    font-size: 700;
    color: black;
    font-family: 'Aharoni', sans-serif;
    text-decoration: none;
    display: inline-block;
    padding: 0 50px;
    margin-right: 10px;
}
.links a:hover{
    transform: scale(1.1);
    transition: all 0.3s;
    color: #ffbc0e;
}


.search input {
    padding: 5px;
    border: none;
    border-radius: 4px;
    margin-right: 5px;
}

.button {
    padding: 5px 15px;
    background-color: #eceff1;
    border: none;
    border-radius: 50px;
    cursor: pointer;
}

.button:hover{
    background-color: #ffbc0e;
    transform: scale(1.1);
}

